.go-home-btn{
position: fixed;
bottom: 28px;
right: 28px;
width: 60px;
height: 60px;
border-radius: 50%;
border-style: none;
box-shadow: 0px 4px 31px rgba(0,0,0,0.15);
}

.go-home-btn:hover{
    cursor: pointer;
    box-shadow: 10px 1px 34px rgba(0,0,0,0.45);
    transition: all 0.3s ease-in-out;
}

.white-bg{
    background: #fff;
}

.gradient-bg{
background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
}

.home-icon{
    width: 40px;
}